import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { FormsModule } from '@angular/forms';
import { HttpModule } from '@angular/http';
import { CommonModule } from '@angular/common';
import { RouterModule, Routes } from '@angular/router';

import { AppComponent } from './app.component';
import { NavbarComponent } from './navbar/navbar.component';
import { CarouselComponent } from './carousel/carousel.component';
import { RestorationServicesComponent } from './restoration-services/services.component';
import { ContactUsComponent } from './contactUs/contactUs.component';
import { ReviewListComponent } from './review/review.component';
import { whyUsComponent } from './whyUs/whyUs.component';
import { FooterComponent } from './footer/footer.component';

const appRoutes: Routes = [
    { path: '#whyUs', component: whyUsComponent },
    { path: '#services', component: RestorationServicesComponent },
    { path: '#contactUs', component: ContactUsComponent },
    {
        path: '',
        redirectTo: '',
        pathMatch: 'full'
    },
    {
        path: '**', component: AppComponent
    }
]

@NgModule({
  declarations: [
    AppComponent,
    NavbarComponent,
    CarouselComponent,
    RestorationServicesComponent,
    ContactUsComponent,
    ReviewListComponent,
    whyUsComponent,
    FooterComponent
  ],
  imports: [
    BrowserModule,
    FormsModule,
    HttpModule,
    CommonModule, 
    RouterModule.forRoot(appRoutes)
  ],
  providers: [],
  bootstrap: [AppComponent]
})
export class AppModule { }



